.Menu-page-container{
  width: 0vw;
  height: 0;
}

.Menu-page-container-top{
  width: 100vw;
  height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.Menu-page-container-top-item1{
  height: 60%;
}
.Menu-page-container-bottom{
  width: 100vw;
  height: 10vh;
  display: flex;
  justify-content:center;

}
.Menu-inter-container{
  width: 32%;
  height: 7vh;
  justify-content: space-around;
  box-sizing: border-box; 
  display: flex;
  padding: 1%;
  padding-left: 1.7%;
  padding-top: 0.25%;
  flex-direction: row;
  background: rgba(184, 183, 183, 0.5);
  border: 1px solid rgba(255,255,255,0.225);
  border-radius: 15px;
  
}


.Menu-inter-container-item{
  width: 75%;
  box-sizing: border-box; 
  padding: 2%;
  margin-bottom: 10%;
  cursor: pointer;
}
.desktop-img{
  width:100%;
}
.desktop-text{
  font-size: 0.8em;
  text-align: center;

}
.Menu-inter-container-item:hover{
  transform: scale(1.3);
}

.Menu-inter-container-item-clicked{
background-color: rgb(245, 245, 245);

}
.dock-icon {
  /* ... autres styles ... */
  transition: transform 0.2s;
}

.dock-icon.hovered-neighbour {
  transform: scale(1.15);
}