
.Folder-inter-container{
  width: 15vw;
  height: 22vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.Folder-inter-container-item1{
  width: 25%;
  height: 45%;
  cursor: pointer;
  padding-left: 2%;
  padding-right: 2%;

}
.Folder-inter-container-item1:hover, .download-icon:hover {
  background-color: rgb(255, 255, 255);
}

.download-icon {
  position: absolute;
  top: 70%;
  left: 45%;
  width: 60px;
  cursor: pointer;
  padding: 0.5%;

}

.folder-icon{
  margin-left:8.5% ;
  width: 85%;
}

.download-icon:hover{
  background-color: rgb(255, 255, 255);

}
.desktop-text1{
  font-size: 0.8em;
  text-align: center;
}